.u-responsive-image {
    position: relative;
    overflow: hidden;

    &--background {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    &__inner {
        height: 0;
        width: 100%;
        padding-bottom: 100%;
        overflow: hidden;
        position: relative;

        &--1-1 {
            padding-bottom: 100%;
        }

        &--2-1 {
            padding-bottom: percentage(1, 2);
        }

        &--1-2 {
            padding-bottom: percentage(2, 1);
        }

        &--3-2 {
            padding-bottom: percentage(2, 3);
        }

        &--2-3 {
            padding-bottom: percentage(3, 2);
        }

        &--4-3 {
            padding-bottom: percentage(3, 4);
        }

        &--3-4 {
            padding-bottom: percentage(4, 3);
        }

        &--16-9 {
            padding-bottom: percentage(9, 16);
        }

        &--9-16 {
            padding-bottom: percentage(16, 9);
        }

        &--9-16-mobile-1-1 {
            padding-bottom: percentage(16, 9);

            @include mq(md) {
                padding-bottom: 100%;
            }
        }

        &--auto {
            padding-bottom: unset;
            height: auto;

            .u-responsive-image__img {
                width: 100%;
                height: auto;
                position: relative;
            }
        }

        &--adopt {
            padding-bottom: unset;
            height: 100%;
        }
    }

    &__img {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;

        &--cover {
            object-fit: cover;
        }

        &--contain {
            object-fit: contain;
        }

        &--top {
            object-position: top;
        }

        &--center {
            object-position: center;
        }

        &--bottom {
            object-position: bottom;
        }
    }
}
